@media screen and (max-width: 1024px) {
  body {
    font-size: 14px;
  }

  .preview {
  
    .header {
      &__menu {
        &-item {
          display: none;
  
          &-menu {
            display: block;
          }
        }
      }

      &.opened {
        max-height:999px;
        height: 100vh;
        background-color: rgba($color: #000000, $alpha: 0.8);

        .header-mobile {
          transform: scaleY(1) translate(-50%, -50%);
        }
      }
    }
  }
}